import React from "react";
import Logo from "../../../../assets/img/png/logo-izq-proguias-san-pedro.png";
import SocialLink from "../../SocialLinks";

import "./MyInfo.scss";

export default function MyInfo() {
  return (
    <div className="my-info">
      <img src={Logo} alt="Pro Guas San Pedro de Atacama" />
      <h4>
        Ampla tus conocimientos como Gua, nete a una comunidad de Guas certificados en San Pedro y obten los cursos ms nuevos.
      </h4>
      <SocialLink />
    </div>
  );
}
